@import url(https://fonts.googleapis.com/css?family=Roboto:100,100italic,300,300italic,400,400italic);
.logged {
  text-decoration: none !important;
  color: unset !important;
  margin-right: 40px;
}

span {
  font-size: 80%;
}

body {
  background-color: #fefcfb;
}

.landing,
.usuarios,
.grupos .practicas,
.compuestos,
.componentes {
  display: -webkit-flex;
  display: flex;
  height: 90vh;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 3rem;
}

.swal-title {
  font-family: "Lexend", "Arial";
}

.swal-text {
  font-family: "Lexend", "Arial";
}

.swal-button {
  background-color: #034078;
  color: #fefcfb;
  padding: 0.3rem 4rem;
  border-radius: 0.15rem;
  box-shadow: none !important;
  width: 100%;
  font-size: 16px;
  font-family: "Lexend", "Arial";
  font-weight: 200;
}

.swal-button:hover {
  background-color: #1282a2 !important;
  color: #fefcfb;
}

.swal-content__input {
  border: 2px solid #1282a2 !important;
  height: auto;
  font-family: "Lexend";
  border-radius: 0.25em;
}

.ant-space-horizontal {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

