  text-align: center;
  padding: 4em 0 8em 0;
  box-shadow: inset 0 1px 0 0 #e6e6e6;
}

#footer .copyright {
  margin-top: 3em;
  font-size: 0.8em;
  color: #aaa;
}

#footer .copyright a {
  color: inherit;
}

#footer ul.icons a {
  box-shadow: inset 0 0 0 1px #d6d6d6;
}

/* Wide */

@media screen and (max-width: 1680px) {
  /* Basic */

  body,
  input,
  textarea,
  select {
    font-size: 15pt;
    line-height: 1.75em;
  }
}

/* Normal */

@media screen and (max-width: 1280px) {
  /* Basic */

  body,
  input,
  textarea,
  select {
    font-size: 13pt;
    line-height: 1.65em;
  }

  /* Feature Icon */

  .feature-icon {
    margin-bottom: 2em;
  }

  /* Header */

  #header {
    padding: 12em 0 12em 0;
  }

  /* Main Sections */

  .main > header {
    padding: 4em 0 4em 0;
  }

  .main > .content {
    padding: 4em 0 4em 0;
  }
}

/* Narrow */

@media screen and (max-width: 1000px) {
  /* Basic */

  header,
  footer,
  h2,
  h3,
  h4,
  h5,
  h6,
  header > p {
    text-align: center;
  }

  /* Sections/Article */

  section,
  article {
    margin: 0 0 2.5em 0 !important;
  }

  .row > section,
  .row > article {
    margin: 0 0 2.5em 0 !important;
  }

  /* Table */

  .table-wrapper {
    width: 100%;
    overflow-x: scroll;
    padding-left: 1px;
    -webkit-overflow-scrolling: touch;
  }

  /* Header */

  #header {
    margin: 0 !important;
    padding: 8em 2em 8em 2em;
  }

  #header header p {
    margin-top: 1em;
  }

  #header footer {
    padding-top: 1.25em;
  }

  /* Main Sections */

  .main {
    margin: 0 !important;
  }

  .main > header {
    padding: 3.5em 2em 3.5em 2em;
  }

  .main > header h2 {
    font-size: 1.85em;
  }

  .main > header br {
    display: none;
  }

  .main > header p {
    margin: 1.5em 0 0 0;
  }

  .main > .content {
    padding: 3.5em 20px 3.5em 20px;
  }

  .main > .content > .container > :last-child {
    margin-bottom: 0 !important;
  }

  /* Footer */

  #footer {
    margin: 0 !important;
    padding: 3em 0 3em 0;
  }

  #footer .copyright {
    margin-top: 2em;
  }
}

/* Mobile */

@media screen and (max-width: 736px) {
  /* Basic */

  body,
  input,
  textarea,
  select {
    font-size: 11pt;
  }

  /* Sections/Article */

  section,
  article {
    margin: 0 0 1.5em 0 !important;
  }

  .row > section,
  .row > article {
    margin: 0 0 1.5em 0 !important;
  }

  /* Button */

  .button {
    padding-left: 0;
    padding-right: 0;
    width: 100%;
    max-width: 24em;
  }

  /* Icons */

  ul.icons li {
    padding-left: 0.35em;
  }

  /* Menu */

  ul.menu li {
    border: 0;
    padding: 0;
    display: block;
    margin: 1em 0 1em 0;
  }

  /* Header */

  #header {
    padding: 6em 20px 6em 20px;
  }

  #header > header {
    padding: 0 1em 0 1em;
  }

  /* Main Sections */

  .main > header {
    padding: 3em 20px 3em 20px;
  }

  .main > header h2 {
    font-size: 1.5em;
  }

  .main > .content {
    padding: 3em 20px 3em 20px;
  }

  .main > .content h3 {
    font-size: 1.25em;
  }
}
